/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea,
p {
  line-height: 1.5;
  color: #fff;
  font-family: Exo, sans-serif;
  font-size: 1rem;
  padding: 2.5%; }

/* Headings */
h1, h2, h3, h4, h5, h6 {
  clear: both; }

/* Copy */
.tag-display {
  font-size: 2rem;
  padding-bottom: 2rem; }

p {
  margin-bottom: 1.5rem;
  font-family: Exo, sans-serif;
  font-weight: 100;
  font-size: 1.25rem; }

dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 1.5rem; }

address {
  margin: 0 0 1.5rem; }

pre {
  background: #ffffff;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: .9375rem;
  line-height: 1.6;
  margin-bottom: 1.6rem;
  max-width: 100%;
  padding: 1.6rem;
  overflow: auto; }

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: .9375rem; }

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: #222222;
  /* Fallback for when there is no custom background color defined. */ }

blockquote, q {
  quotes: "" ""; }
  blockquote:before, blockquote:after, q:before, q:after {
    content: ""; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul, ol {
  margin: 0 0 1.5rem 3rem; }

.tag-cloud-link {
  font-size: 1.25rem; }

ul {
  list-style: none;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0; }
  ul li, ul a {
    margin-right: 20px;
    margin-bottom: 3px;
    font-family: Exo, sans-serif;
    font-weight: 100;
    font-size: 1rem; }
    @media (min-width: 620px) {
      ul li, ul a {
        font-size: 1.25rem; } }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5rem; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 2px solid #00cc00;
  border-radius: 3px;
  background: transparent;
  text-shadow: none;
  color: #00cc00;
  font-size: 1rem;
  line-height: 1;
  padding: .75rem 2.25rem;
  width: 15rem;
  height: 3rem;
  margin: 3rem 0; }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  color: #666;
  border: 2px solid #00cc00;
  border: 2px solid #00cc00;
  background-color: transparent;
  border-radius: 3px;
  padding-left: 3px;
  width: 250px; }
  @media (min-width: 620px) {
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="search"],
    textarea {
      width: 500px; } }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  textarea:focus {
    color: white; }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px;
  border: 2px solid #00cc00;
  background-color: transparent; }

.quote-submission-wrapper :nth-child(1) {
  display: flex;
  flex-direction: column; }

.quote-submission-wrapper div {
  padding: .75rem 0; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00;
  text-decoration: none;
  font-weight: 100; }
  a:visited {
    color: #00cc00; }
  a:hover, a:focus, a:active {
    color: #090; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/* Menus */
.main-navigation {
  clear: both;
  display: block;
  float: left;
  margin-bottom: 1rem; }
  .main-navigation .menu li {
    border-right: 1px solid white;
    line-height: 1; }
  .main-navigation .menu li:nth-child(3) {
    border: none; }
  .main-navigation ul {
    list-style: none;
    margin: 0;
    padding-left: 0; }
    .main-navigation ul ul {
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
      float: left;
      position: absolute;
      top: 1.5rem;
      left: -999rem;
      z-index: 99999; }
    .main-navigation ul ul {
      left: -999rem;
      top: 0; }
    .main-navigation ul li:hover > ul,
    .main-navigation ul li.focus > ul {
      left: 100%; }
    .main-navigation ul li:hover > ul,
    .main-navigation ul li.focus > ul {
      left: auto; }
  .main-navigation li {
    float: left;
    position: relative; }
  .main-navigation a {
    display: block;
    text-decoration: none; }

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
  display: block;
  display: none; }

@media (min-width: 620px) {
  .menu-toggle {
    display: none; }
  .main-navigation ul {
    display: block; } }

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5rem;
  overflow: hidden; }

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%; }

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  display: none; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: .875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/* Posts and pages */
.blog, .page-template-default, .hfeed-site {
  text-align: left;
  background-color: #222222;
  padding: 2.5%; }
  .blog a, .page-template-default a, .hfeed-site a {
    text-decoration: none;
    font-family: Exo, sans-serif;
    font-weight: 100; }
  .blog .entry-meta, .page-template-default .entry-meta, .hfeed-site .entry-meta {
    margin-bottom: 2rem; }

.sticky {
  display: block; }

.archive .hentry {
  margin: 0 0 1.5rem;
  border-bottom: 1px dashed #ffffff; }

.archive .tag-display {
  border-bottom: 1px dashed #ffffff; }

.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5rem 0 0; }

.page-links {
  clear: both;
  margin: 0 0 1.5rem; }

.site-description {
  display: none; }

.site-content {
  position: relative; }

@media (min-width: 620px) {
