  .site-content:after {
    content: '\f10e';
    right: -11rem;
    top: 8rem;
    color: #00cc00; }
  .logo {
    width: 700px;
    margin: 0 auto;
    margin-top: 7rem; }
  body {
    width: 900px;
    margin: 0 auto; }
  .entry-content p {
    font-size: 2.5rem; }
  .page p {
    font-size: 1rem; } }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

